<template>
  <el-dropdown size='mini' style="float: right;">
    <el-link :underline="false" icon="el-icon-more"></el-link>
    <el-dropdown-menu slot="dropdown">
      <!-- <el-dropdown-item>
                  <el-button type="text"  size="mini" @click="download(scope.$index, scope.row)">原始文件</el-button>
                </el-dropdown-item> -->
      <el-dropdown-item>
        <el-button type="text" v-if="check(scope.row.isDir,scope.row.selfName,0)" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button type="text" v-if="check(scope.row.isDir,scope.row.selfName,1)" size="mini" @click="DLNA(scope.row.fid)">投屏</el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,2)" type="text" @click="FileVersion( scope.row)">版本控制</el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,3)" type="text" @click="share(scope.row.fid)">共享</el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,4)" @click="rename(scope.row)" type="text">重命名</el-button>
      </el-dropdown-item>
      <el-dropdown-item>
        <el-button size="mini" v-if="check(scope.row.isDir,scope.row.selfName,5)" type="text">移动到</el-button>
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
  import {
    byteToSize,
    checkType
  } from '../../static/Utils.js'

  export default {
    data() {
      return {

      }
    },
    methods: {
      check(type,name,index){
        var video=[1,1,1,0,1,1];
        var folder=[1,0,0,1,0,0];
        var music=[1,1,1,0,1,1];
        var Other=[1,0,1,0,1,1];
        if(type==1){
          return folder[index]==1;
        }
        switch(checkType(name)){
          case "video":return video[index]==1;break;
        }
        return Other[index]==1;
      },
      rename(row){
        this.$prompt('请输入名称', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(({
          value
        }) => {
          let form = new FormData();
          form.append("targetName", value);
          this.$axios.put('/api/disk/Rename/'+row.fid, form).then(responese => {
            if (responese.data.code === 20000) {
              this.reload();
            }
          });
        });
      },

      share(id) {
        this.$prompt('请输入用户名', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(({
          value
        }) => {
          let form = new FormData();
          form.append("fid", id);
          form.append("email", value);
          this.$axios.put('/api/disk/folder', form).then(responese => {
            if (responese.data.code === 20000) {
              this.reload();
            }
          });
        });
      },

      FileVersion(row) {
        this.$axios.post('/api/v1/FilesVersion/insertFilesVersionControl', {
          fid: row.fid + ''
        }).then(responese => {
          if (responese.data.code == 20000) {
            this.$notify({
              title: '成功',
              message: '添加成功!',
              type: 'success'
            });
          }
        });
      },

      DLNA(fid) {
        this.$prompt('设备名', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({
          value
        }) => {
          this.$axios.get('/api/Files/Share/DLNA', {
            params: {
              fid: fid,
              device: encodeURIComponent(value)
            }
          }).then(responese => {
            console.log(responese);
          })
        });
      },


      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/disk/delete', {
            fid: row.fid
          }).then(responese => {
            if (responese.data.code == 20000) {
              this.$notify({
                title: '成功',
                message: '删除成功!',
                type: 'success'
              });
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
    },
    mounted() {
    },
    props: ['scope'],
  }
</script>

<style>
</style>



